import React, { useState } from 'react';
import styled from 'styled-components';

import FloatBtn from '../../components/atoms/FloatBtn';
import Modal from '../../components/molecules/Modal';

import { ModalPortal } from '../../providers/ModalProvider';
import useMemo from '../../hooks/useMemo';

const Button = styled.button`
  width: 100%;
  padding: 8px 12px;
  font-size: 1rem;
  font-weight: 600;
  color: #47429b;
  background: #a5a0f8;
  border: none;
  border-radius: 3px;
`;

export interface MemoModalProps {}

const MemoModal: React.FC<MemoModalProps> = () => {
  const [isModal, setIsModal] = useState(false);

  return (
    <ModalPortal>
      <FloatBtn
        style={{ position: 'fixed', bottom: 30, right: 30, zIndex: 999 }}
        onClick={() => setIsModal(true)}
      />
      {isModal ? (
        <Modal
          header="Add Memo"
          children={
            <ModalContents handleCloseModal={() => setIsModal(false)} />
          }
          footer={<Button onClick={() => setIsModal(false)}></Button>}
        />
      ) : null}
    </ModalPortal>
  );
};

const SubText = styled.strong`
  font-size: 0.9rem;
  font-weight: 600;
  color: #4a4a4a;
`;
const ModalList = styled.ul`
  margin: 6px auto;
  padding: 0;
  list-style: none;
  & li {
    margin: 8px 0;
  }
`;
const ModalButton = styled(Button)`
  padding: 18px 8px;
  font-size: 1.2rem;
`;
/**
 *   .          .
 *   ( )         
 *            .        
 *     .
 *                 .
 *     MemoModal           .
 */
interface ModalContentsProps {
  handleCloseModal: VoidFunction;
}

const ModalContents: React.FC<ModalContentsProps> = ({ handleCloseModal }) => {
  const { hanleNewMemo } = useMemo();
  return (
    <>
      <SubText>Select Memo Type</SubText>
      <ModalList>
        <li>
          <ModalButton
            onClick={() => {
              hanleNewMemo({
                idx: Date.now().toString(),
                type: 'memo',
                props: '',
              });
              handleCloseModal();
            }}
          >
            Memo
          </ModalButton>
        </li>
        <li>
          <ModalButton
            onClick={() => {
              hanleNewMemo({
                idx: Date.now().toString(),
                type: 'todo',
                props: [],
              });
              handleCloseModal();
            }}
          >
            Todo
          </ModalButton>
        </li>
      </ModalList>
    </>
  );
};

export default MemoModal;
